*{margin: 0;padding: 0;}

body{
  font-family:'Karla', serif;
  color:#333333;
  font-size: 1em;
}

/* Navigation */


nav{
  font-family:'Markazi Text', serif;
  font-size: 1.5em;
  display: flex;
  justify-content: space-between;
  max-width: 500px;
  margin:0 auto;
}

nav ul li{
  list-style: none;
}

.link-no-decor{
  text-decoration: none;
}

.link-no-decor:visited{
  text-decoration: none;
  color: inherit;
}

nav .link-no-decor:hover{
  color:#EE9972;
}

.menu-icon{
  width: 1.5em;
  margin: 0.25em;
}

.nav-links-menu{
  position: absolute;
  top: 2em;
  background-color: white;
  border: 1px solid gray;
}

.nav-links-menu li{
  margin-left: 0.3em;
  margin-right: 0.5em;
}

.nav-links{
  display: none;
}

#nav-logo{
  height: 1.5em;
  margin: 0.25em;
}

/* Hero */


.hero-container{
  background-color: #495E57;
}

.hero{
  color:#EDEFEE;
  display: flex;
  max-width: 500px;
  margin:0 auto;
  justify-content: space-between;
}

.hero-description{
  display: flex;
  flex-direction: column;
}
.hero-description>*{
  margin-left:10px ;
}


.hero *{
  /* border:1px solid red; */
}

.hero p{
  max-width: 200px;
  min-width: 130px;
}

.hero-text{

}

h1{
  color:#F4CE14;
  font-size: 2em;
  margin-top: 0.25em;
}

.subtitle{
  font-size: 1.5em;
  margin-bottom: 0.5em;
}

.btn-primary{
  width: 55%;
  height: 2em;
  border-radius: 10px;
  background-color:#F4CE14;
  font-weight: 700;
  color:#000000;
  min-width: 120px;
  margin-top: 2em;
  margin-bottom: 1em;
  border:none;
}


.hero-image{
  width: 35%;
  border-radius: 16px;
  min-width: 150px;
  margin: 1em;
}

.hero-image-booking{
  width: 70%;
  border-radius: 16px;
  min-width: 210px;
  margin: 1em;
}


/*Specials*/

.specials-header{
  display: flex;
  margin:0 auto;
  max-width: 500px;
  justify-content: space-between;
  align-items: center;
  padding-top: 1em;
  padding-bottom:0.5em ;
}
.specials-header>*{
  margin: 0 0.85em;

}

.specials-header button{
  width: auto;
}

.dish-list{
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 500px;
  margin: 0 auto;
  padding-bottom: 0.5em;
}


.dish-list>*{
  margin:0.5em 1em;
}

.dish-card{
  background-color:#EDEFEE;
  display: flex;
  flex-direction: column;
  border-radius: 16px 16px 16px 16px;
  border: 1px solid rgb(215, 212, 212);
}

.dish-image{
  width: 100%;
  border-radius: 16px 16px 0 0;
}

.dish-info{
  display: flex;
  flex-direction: column;
  padding: 0.5em;
}

.dish-info>*{
  margin-top: 0.5em;
}

.dish-header{
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.dish-price{
  font-weight: 700;
  color:#EE9972 ;
}

button:hover{
  background-color:#EE9972 ;
}

/*footer*/

.footer{
  background-color: #495E57;
  height: 50px;
  display: flex;
  padding-top: 1em;
  justify-content: center;
  color:#EDEFEE;
}

/*Table Form*/

.form-container{
  margin:0 auto;
  max-width: 500px;
}

.table-form{
  display: flex;
  flex-direction: column;
  align-items: center;
}

.form-title{
  margin: 0.75em 0.3em;
  padding-bottom: 0.2em;
  border-bottom: 1px solid rgb(222, 217, 217);
}

form label {
  display: block;
  margin-bottom: 1em;
  margin-left: 0.5em;
  font-weight: 700;
}

form input,
form select {
  margin-bottom: 1em;
  margin-left: 0.5em;
  background-color: #effdf8;
  border-radius: 4px;
  height: 1.5em;
  border:0.5px solid gray;
}

form input:focus,
form select:focus {
  box-shadow: 0 0 8px rgba(99, 243, 166, 0.5);

}
.date-input{
  margin-left: 31px;
  height: 1.5em;
  padding-left: 0.5em;
}
.time-input{
  margin-left: 30px;
  height:1.5em;
  margin-right: 0.4em;
}
.minutes-input{
  height:1.5em;
}
.minutes-input,.time-input{
  width:51px ;
}

.people-input{
  margin-left: 18px;
  padding-left: 0.5em;
  width: 110px;

}
.location-input{
  margin-left: 10px;
  width: 120px;
  height:1.55em;
}

.form-container button{
  width: 200px;
  margin-top: 0;
  margin-left: 0.5em;
}

.firstname-input,.lastname-input,
.email-input,.phone-input{
  padding-left: 0.5em;
  width:240px;
}

.lastname-input{
  margin-left: 12px;
}

.email-input{
  margin-left: 40px;
}

.phone-input{
  margin-left: 35px;
}
.confirmation-text{
  margin-left: 0.5em;
  margin-bottom: 1em;
  line-height: 1.3;
}

.checkbox-container{
  display: flex;
  margin-left: 0.5em;
  margin-bottom: 1em;
  align-items: center;
}
.checkbox-container input{
  margin-right: 0.5em;
}



@media (min-width: 800px) {

  nav{
    max-width: 1000px;
    margin:0 auto;
  }

  .menu-icon{
    display: none;
  }
  .nav-links-menu{
    position: initial;
    background-color: initial;
    border: initial;
  }

  .nav-links,.nav-links-menu{
    display: flex;
    justify-content: space-around;
    align-items: center;
    width:100%;
  }

  .specials-header{
    max-width: 1000px;
  }
  .dish-list{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    align-items: start;
    max-width: 1000px;

  }

  .dish-card{
    height: 400px;
  }

  .footer{
    height: 70px;
  }
}